import { SEC_TO_MS } from "cfg/const"
import { SERVICES_HOSTNAME, LIST_USER_SESSIONS_ENDPOINT, GET_SESSION_DATA_ENDPOINT, MESSAGE_TYPES, GET_SUMMARY_DATA_ENDPOINT, GET_USER_TRIGGERS_ENDPOINT, SAVE_USER_TRIGGERS_ENDPOINT, GET_HEALTH_TABLE_ENDPOINT, GET_HEALTH_SERIES_ENDPOINT, SUBMIT_NOTES_ENDPOINT, GET_USER_DISPOSITIONS } from "cfg/endpoints"

import { logInfo, sleep } from "core"
import { SubmitNoteParams } from "interfaces/db"
import { jsonSafeToSessionDataResult, jsonSafeToSummaryDataResult, jsonSafeToHealthTableResult, jsonSafeToTriggerListDataResult, jsonSafeToUserSessionResult, SessionDataResult, SummaryDataResult, HealthTableResult, TriggerDataResult, TriggerListDataResult, UserSessionsResult, HealthSeriesResult, jsonSafeToHealthSeriesResult, DispositionsDataResult, jsonSafeToDispositionsDataResult } from "interfaces/services"
import { simpleGet, simplePost } from "network"

const _LOG_SCOPE = `[Trellus Web][Services]`

export class ServicesManager {
  apiKey: string | null
  hostname: string
  mock: boolean
  hasInitialized: boolean
  summaryDataServiceManager: SummaryDataServiceManager = new SummaryDataServiceManager()
  healthTableDataServiceManager: HealthTableDataServiceManager = new HealthTableDataServiceManager()
  healthSeriesDataServiceManager: HealthSeriesDataServiceManager = new HealthSeriesDataServiceManager()
  listUserSessionsServiceManager: ListUserSessionsServiceManager = new ListUserSessionsServiceManager()
  sessionDataServiceManager: SessionDataServiceManager = new SessionDataServiceManager()
  triggerServiceManager: TriggerServiceManager = new TriggerServiceManager()
  getDispositionServiceManager: GetUserDispositionsServiceManager = new GetUserDispositionsServiceManager()

  constructor() {
    // initialize the api key from search params
    this.apiKey = (new URLSearchParams(window.location.search)).get('apiKey')
    this.hostname = (new URLSearchParams(window.location.search)).get('forceServicesHostname') ?? SERVICES_HOSTNAME
    this.mock = (new URLSearchParams(window.location.search)).get('mock') === 'true'
    this.hasInitialized = false

    // and overwrite on message
    window.addEventListener('message', this.handleMessage.bind(this))
    logInfo(`${_LOG_SCOPE} Set up service manager`)
    
    // set a timeout for when we are done initializing
    setTimeout(() => {
      logInfo(`${_LOG_SCOPE} Initialization timeout completed`)
      this.hasInitialized = true
    }, 5 * SEC_TO_MS)
  }

  handleMessage(event: MessageEvent) {
    switch (event.data['type']) {
      case MESSAGE_TYPES.APP_TO_EXTERNAL_SET_EXTENSION_INFO:
        logInfo(`${_LOG_SCOPE} Setting extension info`)
        this.apiKey = event.data['apiKey'] ?? this.apiKey
        this.hostname = event.data['forceServicesHostname'] ?? this.hostname
        this.hasInitialized = true
        break
      case MESSAGE_TYPES.APP_TO_EXTERNAL_CHECK_IS_LOADED:
        logInfo(`${_LOG_SCOPE} Responding to check`)
        window.postMessage({type: MESSAGE_TYPES.EXTERNAL_TO_APP_IS_LOADED})
        break
      case MESSAGE_TYPES.EXTERNAL_TO_APP_IS_LOADED:
        break
    }
  }

  async getApiKey(): Promise<string | null> {
    while (!this.hasInitialized) {
      if (this.apiKey != null)
        return this.apiKey
      await sleep(0.5 * SEC_TO_MS)
    }
    return this.apiKey ?? null
  }

  async getSummaryData(): Promise<SummaryDataResult | null> {    
    const apiKey = await this.getApiKey()
    if (apiKey == null) {
      logInfo(`${_LOG_SCOPE} Unable to find api key for summary data`)
      return null
    }
    return this.summaryDataServiceManager.getResult(apiKey, this.hostname, this.mock)
  }

  async getHealthTableData(): Promise<HealthTableResult | null> {
    const apiKey = await this.getApiKey()
    if (apiKey == null) {
      logInfo(`${_LOG_SCOPE} Unable to find api key for infrastructure health table data`)
      return null
    }
    return this.healthTableDataServiceManager.getResult(apiKey, this.hostname, this.mock)
  }

  async getHealthSeriesData(field: string): Promise<HealthSeriesResult | null> {
    const apiKey = await this.getApiKey()
    if (apiKey == null) {
      logInfo(`${_LOG_SCOPE} Unable to find api key for infrastructure table plot data`)
      return null
    }
    return this.healthSeriesDataServiceManager.getResult(apiKey, this.hostname, this.mock, field)
  }

  async getListUserSessions(
    userId?: string, 
    maxStartJsonSafe?: number,
    requiredDispositions?: string[],
  ): Promise<UserSessionsResult | null> {
    const apiKey = await this.getApiKey()
    if (apiKey == null) return null
    return this.listUserSessionsServiceManager.getResult(apiKey, this.hostname, userId, maxStartJsonSafe, requiredDispositions)
  }

  async getSessionData(sessionId: string): Promise<SessionDataResult | null> {
    const apiKey = await this.getApiKey()
    if (apiKey == null) return null
    return this.sessionDataServiceManager.getResult(apiKey, this.hostname, sessionId)
  }

  async submitNotes(sessionId: string, notesParams: SubmitNoteParams) : Promise<Record<string, any> | null> {
    const apiKey = await this.getApiKey()
    if (apiKey == null) return null
    return this.sessionDataServiceManager.postSubmitNotes(apiKey, this.hostname, sessionId, notesParams)
  }

  async getTriggerData(): Promise<TriggerListDataResult | null> {
    const apiKey = await this.getApiKey()
    if (apiKey == null) return null
    return this.triggerServiceManager.getResult(apiKey, this.hostname)
  }

  async postTriggerData(triggerParams: TriggerListDataResult): Promise<Record<string, any> | null> {
    const apiKey = await this.getApiKey()
    if (apiKey == null) return null
    return this.triggerServiceManager.postParams(apiKey, this.hostname, triggerParams)
  } 

  async getUserDispositions(
    userId?: string, 
  ): Promise<DispositionsDataResult | null> {
    const apiKey = await this.getApiKey()
    if (apiKey == null) return null
    return this.getDispositionServiceManager.getResult(apiKey, this.hostname, userId)
  }


}

abstract class CachingMockingDataServiceManager<ResultType, Params extends Array<any> = []> {
  // TODO extend to the ListUserSessionsServiceManager and GetSessionDataServiceManager. Use a hash map?
  lastParams: Array<any> = [null, null]
  cachedPromise: Promise<ResultType | null> | null = null

  async getResult(
    apiKey: string,
    hostname: string,
    mock: boolean,
    ...rest: Params
  ): Promise<ResultType | null> {
    // return mock result if requested
    if (mock) return this._getResultFromMock(...rest)

    // check cache first
    if (this.cachedPromise != null && [apiKey, hostname, ...rest].every((x, i) => x === this.lastParams[i])) {
      return this.cachedPromise
    }

    // make a promise resolving to session data
    this.lastParams = [apiKey, hostname, ...rest]
    this.cachedPromise = this._getResultFromService(apiKey, hostname, ...rest)
    return this.cachedPromise
  }

  abstract _getResultFromMock(...rest: Params): ResultType
  abstract _getResultFromService(apiKey: string, hostname: string, ...rest: Params): Promise<ResultType | null>
}

