import { Filter, FilterProps } from "components/Filter/Filter";
import { findParentByCallable } from "lib/user-agent";
import React, { RefObject } from "react";

export type TableState = {
  showFilterModal: boolean[]
}

export type TableProps<ItemType> = {
  headers: ({label: string | JSX.Element, filterProps?: FilterProps})[],
  keyFunc: (item: ItemType) => string,
  columnFuncs: ((item: ItemType) => string | JSX.Element)[],
  items: ItemType[],
  onRowClick?: (item: ItemType, i: number, col_idx?: number) => void,
  highlightedItemIdx: number
}

export class Table<ItemType> extends React.Component<TableProps<ItemType>, TableState> {
  _keyToItemIdx: Map<string, number>
  _handleClickBound: (event: React.MouseEvent<HTMLElement>) => void
  _observer: RefObject<HTMLTableRowElement>

  constructor(props: TableProps<ItemType>) {
    super(props)
    this._keyToItemIdx = new Map(this.props.items.map((x, i) => [this.props.keyFunc(x), i]))
    this._handleClickBound = this._handleClick.bind(this)
    this._observer = React.createRef();
    this.state = {
      'showFilterModal': new Array(this.props.headers.length).fill(false)
    }
  }

  componentDidUpdate() {
    this._keyToItemIdx = new Map(this.props.items.map((x, i) => [this.props.keyFunc(x), i]))
  }

  _handleClick (event: React.MouseEvent<HTMLElement>) {
    // exit early if there is no row to click on
    const onRowClick = this.props.onRowClick
    if (onRowClick == null) return

    // find the containing table row
    const target = event.target as HTMLElement
    const targetRow = findParentByCallable(target, (target) => target.tagName === 'TR')
    
    // get the associated item by key
    if (targetRow == null) return
    const targetKey = targetRow.getAttribute('data-key')
    if (targetKey == null) return
    const itemIdx = this._keyToItemIdx.get(targetKey)
    if (itemIdx == null) return
    const colIdx = Array.prototype.indexOf.call(targetRow.children, findParentByCallable(target, (target) => target.tagName === 'TD'))

    // fire the callback
    onRowClick(this.props.items[itemIdx], itemIdx, colIdx === -1 ? undefined : colIdx)
    event.stopPropagation()
  }

  static makeCell(
    contents: React.ReactNode, extraClasses: string = "",
    paddingX: string = "px-1", paddingY: string = "py-3", textAlign: string = "text-center",
  ): JSX.Element {
    const className = [extraClasses, paddingX, paddingY, textAlign].join(" ").trim()
    return <td className={className} style={{width: 'fit-content', maxWidth: '100px'}}>{contents}</td>
  }

  _updateFilterState(idx: number) {
    let showFilterModal = [...this.state.showFilterModal];
    showFilterModal[idx] = !showFilterModal[idx];
    this.setState({ showFilterModal }); 
  }

  render(): JSX.Element {
    // make rows
    const trClassNameClickable = this.props.onRowClick == null ? "" : " cursor-pointer hover:bg-slate-200"
    const trClassNameNonHighlighted = "odd:bg-slate-50 even:bg-slate-100 border-b border-slate-300" + trClassNameClickable
    const trClassNameHighlighted = "bg-green-100 border-b border-slate-300" + trClassNameClickable
    const rowElements = this.props.items.map((x, i) => {
      const key = this.props.keyFunc(x)
      const trClassName = i === this.props.highlightedItemIdx ? trClassNameHighlighted : trClassNameNonHighlighted
      const isLastElement = this.props.items.length === i + 1;
      return (
        <tr key={key} className={trClassName} data-key={key} ref={isLastElement ? this._observer : undefined}>
          {this.props.columnFuncs.map((y, j) => {
            let contents = y(x)
            if (!(React.isValidElement(contents) && contents.type == "td")) {
              contents = Table.makeCell(contents)
            }
            return <React.Fragment key={j}>{contents}</React.Fragment>
          })}
        </tr>)
    })
    
    const bodyClass = this.props.onRowClick == null ? "" : 'cursor-pointer'
    const bodyOnClick = this.props.onRowClick == null ? undefined : this._handleClickBound

    const headers = this.props.headers.map((x, i) => {
      if (x.filterProps) {
        return (
        <th className="py-3 px-2 text-center align-bottom w-fit" key={i}>
            <div className="flex flex-row items-center">
              <img className="w-6 h-6 cursor-pointer mr-1" onClick={() => this._updateFilterState(i)} src='three-horizontal.svg'></img>
              {x.label}
            </div>

          {this.state.showFilterModal[i] ? 
              <div className="absolute">
              <Filter {...x.filterProps}></Filter>
              </div> : null}
        </th>
        )
      } else {
        return (<th className="py-3 px-2 text-center align-bottom w-fit" key={i}>{x.label}</th>)
      }
    })

    return (
      <div className='h-full'>
      <table className="min-w-max drop-shadow-md">
        <thead className="top-0 sticky">
          <tr className="bg-slate-200 uppercase text-sm">
            {headers}
          </tr>
        </thead>
        
        <tbody className={bodyClass} onClick={bodyOnClick}>
          {rowElements}
        </tbody>
      </table>
      </div>
    )
  }
}