import React from 'react';

import {
  Chart as ChartJS,
  ArcElement,
  TimeScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend,
  ChartOptions,
  TooltipItem
} from 'chart.js';
import { Doughnut, Line } from 'react-chartjs-2';
import "chartjs-adapter-date-fns";

import { enUS } from 'date-fns/locale'
import { formatInTimeZone } from 'date-fns-tz'
import { add as dateFnsAdd, min as dateFnsMin, format as dateFnsFormat } from 'date-fns'

import { Table, TableProps } from 'components/Table';
import { SummaryDataResult, HealthTableResult, RealtimeHealthRecord, HealthSeriesResult } from 'interfaces/services';
import { getServicesManager } from 'services';
import { SEC_TO_HOUR } from 'cfg/const';
import { makeColors } from 'plotting';
import { groupByKey } from 'packing';
import { sort } from 'core';

import './text-vertical.css';
import { HTTP_STATUS_NAME_OK, HTTP_STATUS_NAMES } from 'network';

ChartJS.register(
  ArcElement,
  TimeScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend
);

type AnalyticsState = {
  summary: SummaryDataResult | null
  health_table: HealthTableResult | null
  plot_field: string | null
  plot_data: HealthSeriesResult | null
}

export class Analytics extends React.Component<{}, AnalyticsState> {
  constructor(props: {}) {
    super(props);
    this.state = {summary: null, health_table: null, plot_field: null, plot_data: null}
  }

  componentDidMount(): void {
    getServicesManager().getSummaryData().then((x) => this.setState({summary: x}))
    getServicesManager().getHealthTableData().then((x) => this.setState({health_table: x, plot_field: "latency"}))
  }

  componentDidUpdate(prevProps: {}, prevState: AnalyticsState) {
    if (this.state.plot_field !== prevState.plot_field) {
      if (this.state.plot_field === null) return
      const saved_field = this.state.plot_field
      getServicesManager().getHealthSeriesData(saved_field).then((x) => {
        if (this.state.plot_field == saved_field) {  // hasn't changed again in the meantime
          this.setState({plot_data: x})
        }
      })
    }
  }

  _renderWeeklyActivity(): JSX.Element {
    const weekly_activity = (this.state.summary ?? {}).weekly_activity
    if (weekly_activity == null) return <div>Loading Analytics...</div>

    const options: ChartOptions<'line'> = {
      responsive: true,
      interaction: {
        mode: 'x' as const,
        intersect: false,
      },
      plugins: {
        tooltip: {
          callbacks: {
            title: function(context: TooltipItem<'line'>[]) {
              return weekEndToLabel(dateFnsMin(context.map((x) => x.parsed.x)))
            }
          }
        }
      },
      scales: {
        x: {
          adapters: {date: {locale: enUS}},
          type: 'time' as const,
        },
        y: {
          type: 'linear' as const,
          display: true,
          position: 'left' as const,
          beginAtZero: true,
        },
        y1: {
          type: 'linear' as const,
          display: true,
          position: 'right' as const,
          grid: {
            drawOnChartArea: false,
          },
          beginAtZero: true,
        },
      },
    };
    
    const colors = makeColors(2)
    const borderColors = makeColors(2, false)
    const data = {
      datasets: [
        {
          label: 'Calls',
          data: weekly_activity.map((data) => ({x: data.week_end, y: data.num_sessions})),
          borderColor: borderColors[0],
          backgroundColor: colors[0],
          yAxisID: 'y',
        },
        {
          label: 'Talk time (hours)',
          data: weekly_activity.map((data) => ({x: data.week_end, y: data.duration * SEC_TO_HOUR})),
          borderColor: borderColors[1],
          backgroundColor: colors[1],
          yAxisID: 'y1',
        },
      ],
    };

    return (
      <React.Fragment>
        <div className='flex text-xl bg-slate-200 p-2 rounded'>Weekly activity</div>
        <div className='mx-8 mt-8 mb-16'>
          <Line options={options} data={data}/>
        </div>
      </React.Fragment>
    )
  }

  _renderUserActivity(): JSX.Element | undefined {
    const user_activity = (this.state.summary ?? {}).user_activity
    if (user_activity == null) return undefined

    const labels: string[] = user_activity.map((x) => x.user_name)

    const options: ChartOptions<'doughnut'> = {
      onClick: (_event, elements) => {
        if (elements.length === 0) return
        window.location.href = `https://app.trellus.ai/pages/react-app/#/history?userId=`+user_activity[elements[0].index].user_id
      },
      plugins: {
        tooltip: {
          callbacks: {
            label: function(context: TooltipItem<'doughnut'>) {
              return `${context.label}`
            },
            afterBody: function(context: TooltipItem<'doughnut'>[]) {
              const index = Math.min(...context.map((x) => x.dataIndex))
              return [
                `${user_activity[index].num_sessions} calls`,
                `${(user_activity[index].duration * SEC_TO_HOUR).toFixed(1)} hours`
              ]
            },
          }
        }
      },
    }

    const data = {
      labels: labels,
      datasets: [
        {
          label: 'Duration (hours)',
          data: user_activity.map((data) => data.num_sessions),
          backgroundColor: makeColors(labels.length),
        },
      ],
    };

    return (
      <div>
        <div className='flex text-xl bg-slate-200 p-2 rounded'>Top users</div>
        <div className='mx-8 mt-8 mb-16'>
          <Doughnut options={options} data={data}/>
        </div>
      </div>
    )
  }

  _renderCohortActivity(): undefined | JSX.Element {
    const cohort_activity = (this.state.summary ?? {}).cohort_activity
    if (cohort_activity == null) return undefined

    const options: ChartOptions<'line'> = {
      responsive: true,
      interaction: {
        mode: 'x' as const,
        intersect: false,
      },
      plugins: {
        tooltip: {
          callbacks: {
            title: function(context: TooltipItem<'line'>[]) {
              return weekEndToLabel(dateFnsMin(context.map((x) => x.parsed.x)))
            }
          }
        }
      },
      scales: {
        x: {
          adapters: {date: {locale: enUS}},
          type: 'time' as const,
        },
        y: {
          type: 'linear' as const,
          display: true,
          position: 'left' as const,
          beginAtZero: true,
        },
      },
    };
    
    // split activity by cohort
    const cohortToActivity = groupByKey(cohort_activity, (item) => item.cohort_week_end.getTime())
    const cohorts = sort(Array.from(cohortToActivity.keys()))

    // make datasets
    const colors = makeColors(cohorts.length)
    const borderColors = makeColors(cohorts.length, false)
    const data = {
      datasets: cohorts.map((x, i) => ({
          label: weekEndToLabel(new Date(x)),
          data: cohortToActivity.get(x)?.map((data) => ({x: data.week_end, y: data.num_sessions})),
          borderColor: borderColors[i],
          backgroundColor: colors[i],
          yAxisID: 'y',
        }))
    };

    return (
      <React.Fragment>
        <div className='flex text-xl bg-slate-200 p-2 rounded'>Cohort analysis</div>
        <div className='mx-8 mt-8 mb-16'>
          <Line options={options} data={data}/>
        </div>
      </React.Fragment>
    )
  }

  _renderInfrastructureDetails(): undefined | JSX.Element {
    const table_rows = this.state.health_table
    if (table_rows == null) return undefined

    // format a table
    const classNameOK = "bg-green-300 text-black"
    const classNameWarn = "bg-amber-400 font-bold text-black"
    const classNameError = "bg-red-600 font-bold text-white"
    function threshold(
      value: number | null | undefined,
      warnable: number | undefined = undefined,
      critical: number | undefined = undefined,
      digits: number = 0,
      sign: number = 1,
    ): JSX.Element | undefined {
      if (value === undefined || value === null) return undefined
      return Table.makeCell(value.toFixed(digits),
                            (critical !== undefined && value * sign > critical * sign ? classNameError
                             : warnable !== undefined && value * sign > warnable * sign ? classNameWarn
                             : classNameOK))
    }
    const plot_fields: string[] = [
      'latency', 'latency', 'reports_this_period', 'latency', 'response_code',
      'num_active_audio_connections', 'num_active_client_connections', 'num_active_sessions', 'num_coaching_sessions',
      'num_async_tasks',
      'cpu_utilization', 'memory_utilization', 'disk_utilization',
      'num_global_processes', 'num_global_net_connections', 'num_global_open_files', 'max_descendant_open_files',
    ]
    const tableProps: TableProps<RealtimeHealthRecord> = {
      headers: [
        'host', 'last updated', 'reports/period', 'latency', 'response code',
        'audio connections', 'client connections', 'active sessions', 'coaching sessions',
        'async tasks',
        'cpu%', 'mem%', 'disk%', 'processes', 'net connections', 'all open files', 'max files/process',
      ].map((x) => {return {'label': (<div className="inline-block text-vertical align-text-top align-bottom">{x}</div>)}}),
      items: table_rows,
      keyFunc: (item) => item.hostname + " " + item.is_aggregate,
      columnFuncs: [
        // metadata
        (item: RealtimeHealthRecord) => item.hostname + " " + (item.is_aggregate ? "daily max" : "latest"),
        (item: RealtimeHealthRecord) => dateFnsFormat(item.update_as_of, "MMM d, p"),
        (item: RealtimeHealthRecord) => threshold(item.reports_this_period, 12, 11, 1, -1) ?? "NA",
        (item: RealtimeHealthRecord) => threshold(item.latency, 5, 10, 2) ?? "NA",
        (item: RealtimeHealthRecord) => {
          const status_string = HTTP_STATUS_NAMES.get(item.response_code) ?? item.response_code.toString()
          return Table.makeCell(status_string, status_string === HTTP_STATUS_NAME_OK ? classNameOK : classNameError)
        },
        // app stats
        (item: RealtimeHealthRecord) => item.num_active_audio_connections?.toString() ?? "NA",
        (item: RealtimeHealthRecord) => item.num_active_client_connections?.toString() ?? "NA",
        (item: RealtimeHealthRecord) => item.num_active_sessions?.toString() ?? "NA",
        (item: RealtimeHealthRecord) => item.num_coaching_sessions?.toString() ?? "NA",
        // framework stats
        (item: RealtimeHealthRecord) => item.num_async_tasks?.toString() ?? "NA",
        // OS stats
        (item: RealtimeHealthRecord) => threshold(item.cpu_utilization, 1.0, 2.0, 2) ?? "NA",
        (item: RealtimeHealthRecord) => threshold(item.memory_utilization, 0.75, 0.95, 2) ?? "NA",
        (item: RealtimeHealthRecord) => threshold(item.disk_utilization, 0.75, 0.9, 2) ?? "NA",
        (item: RealtimeHealthRecord) => item.num_global_processes?.toString() ?? "NA",
        (item: RealtimeHealthRecord) => item.num_global_net_connections?.toString() ?? "NA",
        (item: RealtimeHealthRecord) => threshold(item.num_global_open_files, 100000) ?? "NA",
        (item: RealtimeHealthRecord) => threshold(item.max_descendant_open_files, 5000, 9000) ?? "NA",
      ],
      highlightedItemIdx: -1,
      onRowClick: (item: RealtimeHealthRecord, item_idx: number, col_idx?: number): void => {
        if (col_idx === undefined || col_idx < 0 || col_idx >= plot_fields.length) return
        this.setState({plot_field: plot_fields[col_idx]})
      },
    }

    // configure line plots
    const lineOptions: ChartOptions<'line'> = {
      responsive: true,
      interaction: {
        mode: 'x' as const,
        intersect: false,
      },
      plugins: {
        tooltip: {
          callbacks: {
            title: function(context: TooltipItem<'line'>[]) {
              return dateFnsMin(context.map((x) => x.parsed.x)).toString()
            }
          }
        }
      },
      scales: {
        x: {
          adapters: {date: {locale: enUS}},
          type: 'time' as const,
          time: {displayFormats: {
            hour: 'd (eeeee) hb',
          }}
        },
        y: {
          type: 'linear' as const,
          display: true,
          position: 'left' as const,
          beginAtZero: true,
          title: {
            display: true,
            text: this.state.plot_field?.replaceAll('_', ' ') ?? "",
          },
        },
      },
    };
    const datasets = []
    if (this.state.plot_data !== null) {
      const colors = makeColors(this.state.plot_data.size)
      const borderColors = makeColors(this.state.plot_data.size, false)
      let color_idx: number = 0
      for (const [hostname, measurements] of this.state.plot_data.entries()) {
        datasets.push({
          data: measurements.map((point) => ({x: point.update_as_of.valueOf(), y: point.value})),
          borderColor: borderColors[color_idx],
          backgroundColor: colors[color_idx],
          label: hostname,
          yAxisID: 'y',
        })
        ++color_idx
      }
    }

    return (
      <React.Fragment>
        <div className='flex text-xl bg-slate-200 p-2 rounded'>Infrastructure Health</div>
        <div className='mx-8 my-8 text-center'>
          <div className="inline-block"><Table {...tableProps}></Table></div>
        </div>
        <div className='mx-8 mt-8 mb-16 text-center'>
          <Line options={lineOptions} data={{datasets: datasets}} />
        </div>
      </React.Fragment>
    )
  }

  render() {
    return (
      <div className='w-full'>
        {this._renderWeeklyActivity()}
        {this._renderUserActivity()}
        {this._renderCohortActivity()}
        {this._renderInfrastructureDetails()}
      </div>
    )
  }
}


/**
 * Generate a label for a week from the end data
 * @param weekEnd End of week to generate a label for
 */
function weekEndToLabel (weekEnd: Date): string {
  weekEnd = dateFnsMin([weekEnd, new Date()])
  const weekStart = dateFnsMin([dateFnsAdd(weekEnd, {days: -7}) as Date, new Date()])
  return `${formatInTimeZone(weekStart, 'GMT+0', 'dMMM')} - ${formatInTimeZone(weekEnd, 'GMT+0', 'dMMMyy')}`
}
