import React, { RefObject } from 'react';

import { STARRED_DISPOSITION } from "cfg/endpoints";
import { dateToString, durationToString } from 'time';
import { Table, TableProps } from 'components/Table'
import { UserSessionsResultItem } from 'interfaces/services';
import { FilterOption } from 'components/Filter/Filter';

export type SessionListProps = {
  items: UserSessionsResultItem[],
  dispositionLabels?: string[],
  updateFilter: (requiredDispositions?: string[]) => void, 
  gotoItemView: (itemIdx: number) => void,
  isExhausted: boolean
  isViewingItem: boolean
  viewingItemIdx: number
}

type SessionListState = {
  listenerAttached: boolean
  headerFilters: Map<string, FilterOption[]>
}

export class SessionList extends React.Component<SessionListProps, SessionListState> {
  _listRef: RefObject<HTMLDivElement> = React.createRef()

  constructor (props: SessionListProps) {
    super(props)
    // ensure we don't show starred disposition as an option
    const dispositionFilterOptions = (this.props.dispositionLabels ?? []).filter((disposition) => disposition != STARRED_DISPOSITION).map((disposition => {return {'label': disposition, 'value': disposition, 'selected': false}}))
    this.state = {
      listenerAttached: false,
      headerFilters: new Map([
        ['', [{'label': '', 'value': STARRED_DISPOSITION, 'selected': false}]],
        ['Disposition', dispositionFilterOptions]
      ])
    }
  }

  /**
   * functioned called after the filter state is updated correctly 
   * collects all required dispositions from filters to pass to callback
   */
  _updateFilter() {
   const dispositons = Array.from(this.state.headerFilters.values()).flat(1).filter((filterOption) => {
    return filterOption.selected
    }).map((filterOption) => filterOption.value);
    const requiredDispositions = dispositons.length == 0 ? undefined : dispositons
    this.props.updateFilter(requiredDispositions)
  }

  /**
   * updates the filter states on update
   */
  _onFilter(headerLabel: string, filterOption: FilterOption) {
    this.setState((state) => {
      if (!state.headerFilters.has(headerLabel)) return null
      const headerFilter = state.headerFilters.get(headerLabel)!
      const idx = headerFilter.findIndex((o) => o.label == filterOption.label)
      const elementToUpdate = {...headerFilter[idx]}
      
      const copiedMap = new Map(state.headerFilters)
      return {
        'headerFilters': copiedMap.set(headerLabel, [...headerFilter.slice(0, idx), {...elementToUpdate, 'selected': !elementToUpdate.selected}, ...headerFilter.slice(idx + 1)])
      }
    }, this._updateFilter.bind(this))
  }

  getCompressedTableProps(): TableProps<UserSessionsResultItem>  {
    // properties used by table
    const tableProps: TableProps<UserSessionsResultItem> = {
      headers: [
      {'label': '', 'filterProps': {'headerLabel': '', 'filterOptions': this.state.headerFilters.get('') ?? [], onFilterSelected: this._onFilter.bind(this)}}, 
      {'label': 'Prospect'}, {'label': 'Duration'}, 
      {'label': 'Disposition', 'filterProps': {'headerLabel': 'Disposition', 'filterOptions': this.state.headerFilters.get('Disposition') ?? [], onFilterSelected: this._onFilter.bind(this)}}],
      keyFunc: (item: UserSessionsResultItem): string => item.session_id,
      columnFuncs: [
        (item: UserSessionsResultItem) => item.dispositions != null && item.dispositions.includes(STARRED_DISPOSITION) ? "" : "",
        (item: UserSessionsResultItem) => item.person_name,
        (item: UserSessionsResultItem) => durationToString(item.start == null || item.end == null ? 0 : (item.end.getTime() - item.start.getTime())),
        (item: UserSessionsResultItem) => item.dispositions != null ? item.dispositions.replace("," + STARRED_DISPOSITION, "").replace(STARRED_DISPOSITION, "") : '',
      ],
      items: this.props.items,
      onRowClick: (_item: UserSessionsResultItem, i: number): void => 
         {
          this.props.gotoItemView(i)
         },
      highlightedItemIdx: this.props.viewingItemIdx
    }

    return tableProps
  }
  
  getFullTableProps(): TableProps<UserSessionsResultItem>  {
    // properties used by table
    const tableProps: TableProps<UserSessionsResultItem> = {
      headers: [{'label': '', 'filterProps': {'headerLabel': '', 'filterOptions': this.state.headerFilters.get('') ?? [], onFilterSelected: this._onFilter.bind(this)}}, 
      {'label': 'Prospect'}, {'label': 'Time'}, {'label': 'Duration'}, {'label': 'Disposition', 'filterProps': {'headerLabel': 'Disposition', 'filterOptions': this.state.headerFilters.get('Disposition') ?? [], onFilterSelected: this._onFilter.bind(this)}}],
      keyFunc: (item: UserSessionsResultItem): string => item.session_id,
      columnFuncs: [
        (item: UserSessionsResultItem) => item.dispositions != null && item.dispositions.includes(STARRED_DISPOSITION) ? "" : "",
        (item: UserSessionsResultItem) => item.person_name,
        (item: UserSessionsResultItem) => dateToString(item.scheduled_start),
        (item: UserSessionsResultItem) => durationToString(item.start == null || item.end == null ? 0 : (item.end.getTime() - item.start.getTime())),
        (item: UserSessionsResultItem) => item.dispositions != null ? item.dispositions.replace("," + STARRED_DISPOSITION, "").replace(STARRED_DISPOSITION, "") : ''
      ],
      items: this.props.items,
      onRowClick: (_item: UserSessionsResultItem, i: number): void =>  {
        this.props.gotoItemView(i)
      },
      highlightedItemIdx: -1
    }

    return tableProps
  }


  render(): JSX.Element {
  let tableProps: TableProps<UserSessionsResultItem>
    if (this.props.isViewingItem) {
     tableProps = this.getCompressedTableProps()
    } else {
      tableProps = this.getFullTableProps()
    }

  return (
      <div className='flex flex-col items-end mr-1 h-full'>
        <Table {...tableProps}></Table>
      </div> 
  )
  }
}