import React, { RefObject } from 'react';

import { SessionList, SessionListProps } from 'components/SessionList'
import { SessionDetails, SessionDetailsProps } from 'components/SessionDetails';
import { SessionDataResult, UserSessionsResultItem, Word } from 'interfaces/services';
import { getServicesManager } from 'services';
import { logInfo, maybeBack } from 'core';
import { NoteDisposition, SubmitNoteParams } from 'interfaces/db';
import { TRELLUS_NOTE_SOURCE } from 'cfg/endpoints';

type HistoryState = {
  // state associated with loaded data
  items?: UserSessionsResultItem[]
  uniqueDispositions?: string[]
  continueMaxStartJsonSafe?: number | null,
  requiredDispositions?: string[] 

  // state associated with what the user is viewing
  viewingItemIdx: number // both items and pages are zero indexed
  viewingItemState?: SessionDataResult
  isViewingItem: boolean   // true if the user is currently viewing a single item
  itemsPerPage: number
  eventListenerAdded: boolean
}

export class History extends React.Component<{}, HistoryState> {

  _listRef: RefObject<HTMLDivElement> = React.createRef()
  
  state: HistoryState = {
    viewingItemIdx: 0,
    isViewingItem: false,
    itemsPerPage: 20,
    eventListenerAdded: false,
  }

  _gotoPageViewBound = this._gotoPageView.bind(this)
  _gotoItemViewBound = this._gotoItemView.bind(this)
  _gotoPreviousItemBound = this._gotoPreviousItem.bind(this)
  _gotoNextItemBound = this._gotoNextItem.bind(this)
  _onScrollListenerBound = this._onScrollListener.bind(this)
  _addDispositionBound = this._addDisposition.bind(this)
  _removeDispositionBound = this._removeDisposition.bind(this)
  _updateFilterBound = this._updateFilter.bind(this)


  // TODO: this is extremely hacky and necessary since we use the hashrouter. To get rid of this, we must stop using the hash router 
  // which likely means getting of github pages 
  // https://stackoverflow.com/questions/11920697/how-to-get-hash-value-in-a-url-in-js
  _getUserId(): string | undefined {
    if (window.location.hash == undefined) return undefined
    const firstSplit = window.location.hash.split('userId=')
    if (firstSplit == undefined || firstSplit.length <= 1) return undefined
    else return firstSplit[1].split('&')[0]
  }

  async _getUniqueDispositions(): Promise<undefined> {
    const pageUserId = this._getUserId()
    if (this.state.uniqueDispositions !== undefined) return

    const result = await getServicesManager().getUserDispositions(pageUserId)
    if (result == null) return 
    this.setState({uniqueDispositions: result?.dispositions})
  }

  /**
   * Asynchronously extend the known conversations if appropriate.
   * 
   * @param isKnownEndOfScroll if we know we are the end of the scroll, force refresh (triggered by onscroll)
   * @param dispositionsUpdated if we have updated dispositions for our filters, force update our session list
   * @param requiredDispositions the set of disposition filters we will pass to the search query
   * @returns 
   */
   async _maybeExtendConversations(isKnownEndOfScroll? : boolean, dispositionsUpdated?: boolean, requiredDispositions?: string[]): Promise<undefined> {
    // exit early if the current items are consistent with the requested page and are complete
    // formulate request
    const pageUserId = this._getUserId()

    const items = this.state.items
    const continueMaxStartJsonSafe = this.state.continueMaxStartJsonSafe
    const stateIsValid = items !== undefined && continueMaxStartJsonSafe !== undefined
    const hasMoreItems = stateIsValid && continueMaxStartJsonSafe !== null
    const hasEnoughItems = stateIsValid && ((items.length > this.state.viewingItemIdx + this.state.itemsPerPage) && (!isKnownEndOfScroll))
    
    if (stateIsValid && (hasEnoughItems || !hasMoreItems) && (!dispositionsUpdated)) return


    const maxStartJsonSafe = !dispositionsUpdated && hasMoreItems ? continueMaxStartJsonSafe : undefined
    
    // get new items
    const result = await getServicesManager().getListUserSessions(pageUserId , maxStartJsonSafe, requiredDispositions)


    // there is no update if the service call failed
    if (result == null) return

    // update state
    this.setState((state) => {
      // get old items to retain. note: results and continuation is in _descending_ time order
      const lastStateItems = state.items
      const retainItems = !dispositionsUpdated && lastStateItems !== undefined ? lastStateItems : []
      const lastItemStart = (maybeBack(retainItems) ?? {}).scheduled_start
      const newItems = lastItemStart == null ? result.items : result.items.filter((x) => x.scheduled_start < lastItemStart)

      /**
       * default parameters for non-viewing state in case the new filter that we applied 
       */
       let viewingItemIdx = this.state.viewingItemIdx;
       let isViewingItem = this.state.isViewingItem;
       let viewingItemState = this.state.viewingItemState; 
 
       /**
        * if a filter is applied and you are currently viewing item, you need to update
        * if the viewed item matches the current filters, you keep it and update the viewingItemIdx
        */
      if (state.isViewingItem && dispositionsUpdated) {
        const matchesNewFilter = (requiredDispositions ?? []).filter(disposition => !state.items![state.viewingItemIdx].dispositions?.includes(disposition))
        const matchingIdx = result.items.findIndex((result) => result.session_id == this.state.viewingItemState?.session.session_id) 
        if (matchesNewFilter.length == 0 && matchingIdx !== -1) { // possible that it matches but is not in returned list idx
          viewingItemIdx = matchingIdx
          isViewingItem = true;
          viewingItemState = this.state.viewingItemState; 
        } else{
          viewingItemIdx = 0;
          isViewingItem = false;
          viewingItemState = undefined; 
        }
      }

      // exit early if there is no state change. note: this is important to prevent infinite looping
      if (newItems.length === 0 && state.continueMaxStartJsonSafe === result.continueFromJsonSafe)
        return null

      // make update
      return {
        viewingItemIdx: viewingItemIdx,
        isViewingItem: isViewingItem,
        viewingItemState: viewingItemState,
        items: [...retainItems, ...newItems],
        continueMaxStartJsonSafe: result.continueFromJsonSafe,
      }
    })
  }

  /**
   * callback function to update filter 
   * @param requiredDispositions list of dispositions to filter on
   */
  async _updateFilter(requiredDispositions?: string[]) {
    await this._maybeExtendConversations(undefined, true, requiredDispositions)
  }

  /**
   * grabs session data for the current selected item
   * @returns 
   */
  async _updateSelectedData(): Promise<undefined> {
    if (this.state.items === undefined) return
    const item = this.state.items[this.state.viewingItemIdx]

    // get data
    const data = await getServicesManager().getSessionData(item.session_id)
    // if the endpoint failed, set error
    this.setState({viewingItemState: data ?? undefined})
  }

  /**
   * Set the viewingItemIdx to the previous page
   */
  _gotoPageView() {
    this.setState({isViewingItem: false}, this._updateSelectedData)
  }

  /**
   * Switch between viewing a list of items and a single item
   */
  _gotoItemView(viewingItemIdx: number): void {
    this.setState({isViewingItem: true, viewingItemIdx: viewingItemIdx}, this._updateSelectedData)
  }

  /**
   * Set the viewingItemIdx to the previous item
   */
   _gotoPreviousItem() {
    this.setState((state) => ({viewingItemIdx: state.viewingItemIdx - 1}), this._updateSelectedData)
  }

  /**
   * Set the viewingItemIdx to the next item
   */
  _gotoNextItem() {
    this.setState((state) => ({viewingItemIdx: state.viewingItemIdx + 1}), this._updateSelectedData)
  }

  /**
   * Allows user to add a disposition to the current active session 
   * @param dispositionToAdd disposition value to add
   */
  async _addDisposition(dispositionToAdd: {'label': string, 'value': string}) {
    //get the note id to submit the disposition with
    if (!this.state.viewingItemState) return
    let noteId = '';
    const trellusNoteList = this.state.viewingItemState.notes.filter((note) => note.note_source == TRELLUS_NOTE_SOURCE)
    if (trellusNoteList.length == 0) {
      noteId = '' // using empty string if not found (there may be no note created yet)
    } else if (trellusNoteList.length == 1) {
      noteId = trellusNoteList[0].note_id
    } else {
      logInfo('Note source in bad state, should only be one trellus note source note')
      return
    }
    //get all dispositions that match, if noteId is not undefined
    const dispositionToSubmit = this.state.viewingItemState!.dispositions.filter((noteDisposition) => noteDisposition.note_id == noteId).map(
      (noteDisposition) => {return {'label': noteDisposition.label, 'value': noteDisposition.disposition}})
    dispositionToSubmit.push(dispositionToAdd)
    const notesParams: SubmitNoteParams = {'note_source': TRELLUS_NOTE_SOURCE, 'notes': '', dispositions: dispositionToSubmit}
    await getServicesManager().submitNotes(this.state.viewingItemState!.session.session_id, notesParams)

    this.setState((state) => {
      // we have to update the session list items with the correct disposition string
      const currentDispositionString = state.items![this.state.viewingItemIdx].dispositions ?? undefined
      const updatedDispositionString = currentDispositionString ? currentDispositionString + "," + dispositionToAdd.value : dispositionToAdd.value
      const updatedItem = {...state.items![this.state.viewingItemIdx]}
      updatedItem.dispositions = updatedDispositionString

      const selectedItem = this.state.viewingItemState!
      
      const noteDisposition: NoteDisposition = { 
        'disposition': dispositionToAdd.value,
        'label': dispositionToAdd.label,
        'note_id': noteId, 
      }

      const dispositions = [...selectedItem.dispositions, noteDisposition ]

      return {
        items: [...state.items!.slice(0, this.state.viewingItemIdx),updatedItem, ...state.items!.slice(this.state.viewingItemIdx + 1)],
        viewingItemState: {...state.viewingItemState!, dispositions: dispositions} 
      }
    })
  }

  /**
   * Allows user to remove a disposition from the current active session 
   * @param noteDisposition disposition to remove
   */
  async _removeDisposition(noteDisposition: NoteDisposition) {
    const noteId = noteDisposition.note_id
    //get all dispositions that match, if noteId is not undefined
    const dispositionToSubmit = this.state.viewingItemState!.dispositions.filter(
      (noteDisposition) => noteDisposition.note_id === noteId && noteDisposition.label !== noteDisposition.label).map(
      (noteDisposition) => {return {'label': noteDisposition.label, 'value': noteDisposition.disposition}})
    const notesParams: SubmitNoteParams = {'note_source': TRELLUS_NOTE_SOURCE, 'notes': '', dispositions: dispositionToSubmit}
    await getServicesManager().submitNotes(this.state.viewingItemState!.session.session_id, notesParams)
    
    this.setState((state) => {
      // we have to update the session list items with the correct disposition string
      const currentDispositionString = state.items![this.state.viewingItemIdx].dispositions ?? ''
      const updatedDispositionString = currentDispositionString.replace(',' + noteDisposition.disposition, '').replace(noteDisposition.disposition, '')
      const updatedItem = {...state.items![this.state.viewingItemIdx]}
      updatedItem.dispositions = updatedDispositionString

      const selectedItem = this.state.viewingItemState!
      let dispositions = [...selectedItem.dispositions]
      const idx = dispositions.findIndex((o) => {
        return (o.label == noteDisposition.label && o.disposition == noteDisposition.disposition)
      })
      dispositions = [...dispositions.slice(0, idx), ...dispositions.slice(idx+1)]

      return {
        items: [...state.items!.slice(0, this.state.viewingItemIdx),updatedItem, ...state.items!.slice(this.state.viewingItemIdx + 1)],
        viewingItemState: {...state.viewingItemState!, dispositions: dispositions} 
      }
    })
  }


  /**
   * Start loading data on mount
   */
  componentDidMount(): void {
    this._getUniqueDispositions()
    this._maybeExtendConversations()
  }

  /**
   * After state changes try to update conversations
   */
  componentDidUpdate(): void {
    this._getUniqueDispositions()
    this._maybeExtendConversations()
    if (this._listRef.current !== null && !this.state.eventListenerAdded) {
      this._listRef.current.addEventListener('scroll', this._onScrollListenerBound)
      this.setState({eventListenerAdded: true})
    }
  }

  _onScrollListener() : void {
    if (this._listRef.current!.scrollTop + this._listRef.current!.clientHeight >=
      this._listRef.current!.scrollHeight) {
      this._maybeExtendConversations(true)
    }
  }
  /**
   * Render either a list view or a details view
   */
  render(): JSX.Element {
    // show a loading message until there are items
    const items = this.state.items
    if (items == null) return <div>Loading History...</div>

    const sessionListProps: SessionListProps = {
      items: items,
      dispositionLabels: this.state.uniqueDispositions,
      updateFilter: this._updateFilterBound,
      gotoItemView: this._gotoItemViewBound,
      isExhausted: this.state.continueMaxStartJsonSafe === null,
      isViewingItem: this.state.isViewingItem, 
      viewingItemIdx: this.state.viewingItemIdx
    }

    let transcriptElement: JSX.Element 

    if (this.state.isViewingItem) {
      const item = this.state.items![this.state.viewingItemIdx]
      const sessionDetailsProps: SessionDetailsProps = {
        data: this.state.viewingItemState,
        start_time: item.start,
        end_time: item.end,
        prospectName: item.person_name,
        gotoPageView: this._gotoPageViewBound,
        gotoPreviousItem: this._gotoPreviousItemBound,
        gotoNextItem: this._gotoNextItemBound,
        addDisposition: this._addDispositionBound,
        removeDisposition: this._removeDispositionBound,
        hasPreviousItem: this.state.viewingItemIdx > 0,
        hasNextItem: this.state.viewingItemIdx < items.length - 1,
      } 

      transcriptElement = (
        <div className='w-full h-screen overflow-y-auto ml-2 mr-2'>
          <SessionDetails {...sessionDetailsProps}></SessionDetails>
        </div>
      )
    } else {
      transcriptElement = ( 
        <div className='w-full h-screen overflow-y-auto ml-2 mr-2'>
          Click to view a transcript!
        </div>
      )
    }

    return (
      <div className='flex flex-column overflow-y-auto w-full' style={{'userSelect': 'text'}}>
      <div className='h-screen overflow-y-auto min-w-fit w-fit' ref={this._listRef}>
        <SessionList {...sessionListProps}></SessionList>
      </div>
      {transcriptElement}
      </div>
    )
  }
}
