import React from 'react'

export type FilterOption = {
    'label': string,
    'value': string,
    'selected': boolean
}

export type FilterProps = {
    headerLabel: string, 
    filterOptions: FilterOption[]
    onFilterSelected: (headerLabel: string, filterOption: FilterOption) => void
}

export class Filter extends React.Component<FilterProps, {}> {

    constructor(props: FilterProps) {
        super(props)
    }

  _renderCheckItem(filterOption: FilterOption, idx: number) {
    const bgColor = filterOption.selected ? 'bg-lime-100' : 'bg-slate-100'
    const textColor = filterOption.selected ? 'text-black' : 'text-gray-700'
    const mt = (idx == 0) ? null : 'mt-2'
    return (
        <div key={idx} className={`${bgColor} ${textColor} cursor-pointer rounded-md ${mt} border border-black border-solid`} onClick={() => {this.props.onFilterSelected(this.props.headerLabel, {...filterOption, 'selected': !filterOption.selected})}} >
            {filterOption.label}
        </div>
    )
  }

  render(): JSX.Element {
    return (
      <div className='text-xs mt-1 flex flex-col justify-around z-50 rounded-lg p-2' style={{'backgroundColor': 'white', 'minWidth': '80px', 'boxShadow': '2px 1px 1px #03214E'}}>
        {this.props.filterOptions.map((x, i) => this._renderCheckItem(x, i))}
      </div>
    )
  }
}
